.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home, .signup, .signin, .movie {
  background: #FBE4D4;
  color: #fff;
  font-family: 'Poppins',sans-serif;
  font-style: normal;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.home-intro, .signup-wrap, .signin-wrap {
  width: 100%;
}

.home-intro img, .signup-wrap img, .signin-wrap img {
  width: 100%;
}

.home-title, .signup-title, .signin-title {
  position: absolute;
  top: 5%;
  left: 10%;
  font-weight: 700;
  font-size: 50px;
  line-height: 50px;
  
  color: #F91111;
  
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.home-message {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
}

.home-message .message1 {
  font-weight: 700;
  font-size: 64px;
  line-height: 72px;
  padding: 5%;
  text-align: center;

  /* White */

  color: #FFFFFF;
}

.home-message .message2 {
  font-weight: 400;
  font-size: 30px;
  line-height: 30px;
  padding: 3%;
  text-align: center;

  /* White */

  color: #FFFFFF;
}

.home-message .message3 {  
  font-weight: 400;
  font-size: 23px;
  line-height: 23px;
  /* identical to box height */

  text-align: center;

  /* White */

  color: #FFFFFF;
}

.auth {
  display: flex;
  width: 20%;
  padding: 0.5%;
}

.auth button {
  border-radius: 10px;
  border: none;
  padding: 7px 5%;
  margin: 0px 5%;
  color: #00A0B5FF;
  width: 30%;
  border: 1px solid #00A0B5FF;
  cursor: pointer;
  user-select: none;
}

.path div {
  padding: 10px;
}

.auth button a {
  text-decoration: none;
  color: #fff;
}

.signin, .signup {
  overflow: hidden;
  height: 100vh;
}

.signup-form, .signin-form, .movie-form {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(0, 0, 0, 0.8);
  box-sizing: border-box;
  box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
  border-radius: 12px;
  top: 50%;
  left: 50%;
  width: 30%;
  height: 60%;
  transform: translate(-50%, -50%);
  padding: 0 20px;
}

.signup-form h3 :first-child, .signin-form h3 :first-child {
  color: #D6000F; 
  display: inline;
}

.signup-form p {
  font-size: 2vh;
}

.signup-form input, .signin-form input{
  font-size: 2vh;
  width: 100%;
  margin: 10px 0;
  padding: 5px 0;
}

.signup-form .signup-btn, .signin-form .signin-btn {
  background: #ED7D2DFF;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}

.or {
  display: flex;
  width: 100%;
  flex-direction: row;
  height: 1px;
  margin-top: 5%;
}

.or hr {
  width: 45%;
}

.kep-login-facebook {
  width: 70%;
  margin: 10% 15%;
  font-size: 2vh !important;
  font-family: 'Helvetica Neue',Helvetica,Arial,sans-serif !important;
  padding: 2% !important;
}

.welcome {
  display: flex;
  padding: 5px 0px;
  margin-right: 20px;
}

.welcome-text {
  color: #fff;
}

.welcome-text p {
  display: inline;
}
.welcome-text p:first-child {
  font-style: italic;
}

.welcome div {
  margin: 2% 10px;
}

.welcome img {
  border-radius: 50%;
}

.signin-form p, .signup-form p {
  text-align: center;
}

.signin-form p a, .signup-form p a {
  color:#00BDD6FF
}

.signin-form span, .signup-form span {
  font-size: 1.5vh;
  color:#D6000F
}

.welcome span {
  cursor: pointer;
  margin-top: 2%;
}

.add-movie-btn {
  position: absolute;
  top: 20%;
  left: 20%;
  background: red;
  padding: 10px 20px;
  cursor: pointer;
  font-weight: 600;
}

.movie-wrap {
  width: 80%;
  margin: 10% 0 0 10%;
  display: flex;
  justify-content: space-between;
}

.movie-wrap .movie-info {
  width: 50%;
}

.movie-name {
  font-size: 4vh;
  font-weight: 600;
  text-align: center;
  font-style: italic;
  padding-bottom: 3vh;
}

.movie-description {
  font-size: 2vh;
}

.top-bar{
  width: 100%;
  height: 6%;
  background: #F3A974FF;
  border-radius: 0px;
  box-shadow: 0px 0px 1px #21262e, 0px 0px 2px #21262e;
  display: flex;
  justify-content: space-between;
}

.logo {
  font-size: 4vh;
  font-weight: 700;
  padding: 0.5%;
  cursor: pointer;
  width: fit-content;
  color: #fff;
}

.logo:hover {
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5);
}

.intro-slider {
  height: 40vh;
  background-color: #fff;
  color: #000;
}

.movie-intro .movie-image {
  width: 60vh;
  max-height: 38vh;
  border-radius: 50%;
  margin-top: 1vh;
  overflow: hidden;
  height: fit-content;
}

.movie-intro .movie-image img {
  width: 100%;
}

.movie-intro {
  height: 40vh;
  background-color: #F3F4F6FF;
  display: flex !important;
  flex-direction: row;
  justify-content: space-evenly;
}

.movie-intro h1 {
  text-shadow: 0px 1px, 1px 0px, 1px 1px;
  padding: 7% 5%;
  text-align: center;
}

.movie-intro p {
  padding: 1% 5%;
  font-weight: 600;
  font-size: 1.8vh;
}

.movie-title {
  width: 50%;
}

.movie-slider {
  padding: 3vh;
  color: #000;
}

.movie-slider h2 {
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.5)
}

.movie-slider .movie-info {
  display: flex;
  flex-direction: column;
  height: 40vh;
  margin: 0 1.5vw;
  background-color: #fff;
  cursor: pointer;
  border-radius: 15px;
  overflow: hidden;
}

/* .movie-item :hover {
  transform: translateY(-15px);
} */

.slick-list {
  overflow: visible !important;
}

.movie-info .movie-image {
  height: 17vh;
  width: fit-content;
  margin-left: 50%;
  transform: translatex(-50%);
  max-width: 90%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.movie-info .movie-image img {
  height: 100%;
}

.movie-info .rating {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.movie-info .rating span {
  padding-top: 10px;
}

.movie-info span{
  text-align: center;
  font-weight: bold;
  display: block;
  font-size: 2vh;
}

.movie-info p {
  padding: 0 10px;
  font-size: 1.5vh;
}

.movie-info .description {
  background: #0002;
  flex: 2;
}

.message-box {
  position: fixed;
  left: 70%;
  width: 25%;
  top: auto;
  bottom: 0;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

.message-title {
  background: #00A0B5FF;
  padding: 5px;
  color: #000;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
}

.message-content {
  background-color: #FBE4D4;
}

.messages {
  height: 40vh;
  border: 2px solid #fff;
  border-radius: 5px;
}

.input {
  height: 7vh;
  border: 2px solid #fff;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
}

.input .submit-message {
  background: #00A0B5FF;
  border-radius: 5px;
}

.input .submit-message:hover {background-color: rgb(0, 117, 132)}

.input .submit-message:active {
  background-color: rgb(0, 117, 132);
  box-shadow: 0 5px #666;
  transform: translateY(2px);
}

.message-title i:active {
  transform: translateX(2px);
}

.message-title i {
  font-size: 3vh;
  cursor: pointer;
}

.input textarea {
  width: 100%;
  background: inherit;
  font-family: inherit;
}

.messages {
  display: flex;
}
.message {
  width: fit-content;
  padding: 5px 10px;
  border-radius: 20px;
  margin: 5px;
  max-width: 70%;
}

.message.client {
  align-self: flex-end;
  background: #00A0B5FF;
}

.message.server {
  align-self: flex-start;
  color: #00A0B5FF;
  background-color: #fff;
}

.message p {
  margin: 0;
}

.dummy {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
}

.dummy div:first-child {
  margin-top: auto;
}

.eapps-widget-toolbar {
  display: none !important;
}

.cDAUeN .div:nth-child(5) {
  display: none !important;
}

.slick-arrow {
  z-index: 1;
}

.footer {
  background: #F3A974;
  display: flex;
  justify-content: space-around;
  margin-top: 10vh;
  padding: 30px;
}

.footer h3 {
  font-size: 15px;
  color: #000;
}

.footer p {
  font-size: 12px;
  color: #000;
}

.loading-movielist {
  display: flex;
  justify-content: space-around;
}

.user-menu .menu {
  position: absolute;
  z-index: 1;
  background: #fff;
  color: #00A0B5;
  width: 10vw;
  right: 0;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

.user-menu i, .user-menu p {
  display: inline;
  margin: 0 10px;
}

.user-menu > i {
  padding: 10px 15px;
  cursor: pointer;
}

.user-menu > i:hover {
  background: #c2865c;
}

.menu > div {
  margin: 0;
  cursor: pointer;
  padding: 5px 0;
}

.menu .logout:hover, .menu .profile:hover {
  background: #f9f3ee;
}